import ClientService from '../services/client';
import Helper from '@/common/helper';

import {
   FETCH_CLIENTS, CREATE_CLIENT, STORE_CLIENT, 
   EDIT_CLIENT, UPDATE_CLIENT, DELETE_CLIENT
} from './actions.type';

import { SET_CLIENTS, SET_REGIONS, SET_LOADING } from './mutations.type';



const initialState = {
   pageList: null,
   regions: []
};

export const state = { ...initialState };
 
const getters = {
   
};


const actions = {
   [FETCH_CLIENTS](context, params) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         ClientService.fetch(params)
            .then(model => {
               context.commit(SET_CLIENTS, model.pageList);
               if(model.regions && model.regions.length){
                  context.commit(SET_REGIONS, model.regions);
               }
               resolve(model);
            })
            .catch(error => {
               reject(error);
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [CREATE_CLIENT](context) {
      return new Promise((resolve, reject) => {
         ClientService.create()
            .then(model => {
               context.commit(SET_REGIONS, model.regions);
               resolve(model);
            })
            .catch(error => {
               reject(error);      
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [STORE_CLIENT](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         ClientService.store(model)
            .then(CLIENT => {
               resolve(CLIENT);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [EDIT_CLIENT](context, id) {
      return new Promise((resolve, reject) => {
         ClientService.edit(id)
            .then(model => {
               context.commit(SET_REGIONS, model.regions);
               resolve(model);
            })
            .catch(error => {
               reject(error);        
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [UPDATE_CLIENT](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         ClientService.update(model)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [DELETE_CLIENT](context, id) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         ClientService.remove(id)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error));
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   
   
};


const mutations = {
   [SET_CLIENTS](state, pageList) {
      state.pageList = pageList;
   },
   [SET_REGIONS](state, regions) {
      state.regions = regions;
   }
};

export default {
   state,
   actions,
   mutations,
   getters
};
 