import TradeService from '../services/trade';
import Helper from '@/common/helper';

import {
   STORE_TRADE, 
   UPDATE_TRADE, DELETE_TRADE
} from './actions.type';

import { SET_LOADING } from './mutations.type';

const initialState = {
  
};




export const state = { ...initialState };
 
const getters = {
   
};


const actions = {
   
   [STORE_TRADE](context, model) {
      context.commit(SET_LOADING, true);
      console.log(model);
      return new Promise((resolve, reject) => {
         TradeService.store(model)
            .then(id => {
               resolve(id);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [UPDATE_TRADE](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         TradeService.update(model)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [DELETE_TRADE](context, id) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         TradeService.remove(id)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error));
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   
   
};


const mutations = {
   
};

export default {
   state,
   actions,
   mutations,
   getters
};
 