<template>
   <v-navigation-drawer id="app-drawer"  v-model="inputValue"
     app dark floating persistent mobile-break-point="991"  width="260"
   >
   
      <v-layout  class="fill-height"  tag="v-list" column>
         <v-list-tile avatar>
            <v-list-tile-title class="title" style="text-align: center;">
               <span class="cn" style="font-size:1.2rem">IT Manage</span>
            </v-list-tile-title>
         </v-list-tile>
         
         <v-divider/>
         
         <v-list-tile v-for="item in links" :key="item.order"
            :to="item.path"   :active-class="color"
            avatar  class="v-list-item"
         >
            <v-list-tile-action>
               <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-title class="cn"  v-text="item.title"/>
         </v-list-tile>

         
 
         <!-- <v-list-group no-action>
            <template v-slot:activator>
               <v-list-tile  to="/test"   :active-class="color"  avatar  class="v-list-item">
                  <v-list-tile-action>
                     <v-icon>mdi-account</v-icon>
                  </v-list-tile-action>
                  <v-list-tile-title class="cn">
                     帳號管理
                  </v-list-tile-title>
               </v-list-tile>
            </template>
            <v-list-tile v-for="(admin, i) in admins"  :key="i" >
               <v-list-tile-title v-text="admin[0]"></v-list-tile-title>
               <v-list-tile-action>
                  <v-icon v-text="admin[1]"></v-icon>
               </v-list-tile-action>
            </v-list-tile>
         </v-list-group> -->
         

      </v-layout>
   </v-navigation-drawer>
</template>

<script>

import { SET_DRAWER, TOGGLE_DRAWER } from '../store/mutations.type';

export default {
   name: 'TheDrawer',
   props: {
      color: {
         type: String,
         default: 'info'
      },
      image: {
         type: String,
         default: ''
      },
      responsive:{
         type: Boolean,
         default: false
      }
   },
   data () {
		return {
          admins: [
        ['Management', 'people_outline'],
        ['Settings', 'settings']
      ],
      cruds: [
        ['Create', 'add'],
        ['Read', 'insert_drive_file'],
        ['Update', 'update'],
        ['Delete', 'delete']
      ]
		}
   },
   computed: {
      inputValue: {
         get () {
            return this.$store.state.app.drawer
         },
         set (val) {
            this.setDrawer(val)
         }
      },
      routes(){
         if(this.$router) return this.$router.options.routes.filter(item => item.meta.menu);
         return [];         
      },
      links(){
         return this.routes.map(route => this.mapLink(route));
      },
      items () {
         return this.$t('Layout.View.items')
      }
   },
   methods: {
      mapLink(route){
         return {
            name: route.name,
            path: route.path,
            order: route.meta.order,
            icon: route.meta.icon,
            title: route.meta.title
         }
      },
      setDrawer(val){
         this.$store.commit(SET_DRAWER, val);
      },
      toggleDrawer(){
         this.$store.commit(TOGGLE_DRAWER);
      }
	}
}
</script>


<style lang="scss">
  #app-drawer {
    .v-list__tile {
      border-radius: 4px;

      &--buy {
        margin-top: auto;
        margin-bottom: 17px;
      }
    }

    .v-image__image--contain {
      top: 9px;
      height: 60%;
    }

    .search-input {
      margin-bottom: 30px !important;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
</style>


