import DeploymentService from '../services/deployment';
import Helper from '@/common/helper';

import {
   FETCH_DEPLOYMENTS, CREATE_DEPLOYMENT, STORE_DEPLOYMENT, 
   EDIT_DEPLOYMENT, UPDATE_DEPLOYMENT, DELETE_DEPLOYMENT
} from './actions.type';

import { SET_DEPLOYMENTS, SET_LOADING } from './mutations.type';



const initialState = {
   pageList: null
};

export const state = { ...initialState };
 
const getters = {
   
};


const actions = {
   [FETCH_DEPLOYMENTS](context, params) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         DeploymentService.fetch(params)
            .then(model => {
               context.commit(SET_DEPLOYMENTS, model);
               resolve(model);
            })
            .catch(error => {
               reject(error);
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [CREATE_DEPLOYMENT](context) {
      return new Promise((resolve, reject) => {
         DeploymentService.create()
            .then(model => resolve(model))
            .catch(error => {
               reject(error);      
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [STORE_DEPLOYMENT](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         DeploymentService.store(model)
            .then(DEPLOYMENT => {
               resolve(DEPLOYMENT);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [EDIT_DEPLOYMENT](context, id) {
      return new Promise((resolve, reject) => {
         DeploymentService.edit(id)
            .then(model => resolve(model))
            .catch(error => {
               reject(error);        
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [UPDATE_DEPLOYMENT](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         DeploymentService.update(model)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [DELETE_DEPLOYMENT](context, ids) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         DeploymentService.remove(ids)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error));
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   
   
};


const mutations = {
   [SET_DEPLOYMENTS](state, model) {
      state.pageList = model;
   }
};

export default {
   state,
   actions,
   mutations,
   getters
};
 