const links = [
   
   {
      name: 'clients',
      path: '/clients',
      component: () => import('../views/Clients'), 
      meta: { 
         order: 0,
         icon: 'mdi-city-variant-outline',
         title: 'Clients',
         type: 'all',
         menu: 'all'
      }        
   },
   {
      name: 'deployments',
      path: '/deployments',
      component: () => import('../views/Deployments'), 
      meta: { 
         order: 0,
         icon: 'mdi-account',
         title: 'Deployment',
         type: 'all',
         menu: 'all'
      }        
   },
   {
      name: 'manuals',
      path: '/manuals',
      component: () => import('../views/Manuals'), 
      meta: { 
         order: 0,
         icon: 'mdi-account',
         title: 'Manuals',
         type: 'all',
         menu: 'all'
      }        
   },
   {
      name: 'assets',
      path: '/assets',
      component: () => import('../views/asset/Index'), 
      meta: { 
         order: 0,
         icon: 'mdi-briefcase',
         title: 'Assets',
         type: 'all',
         menu: 'all'
      }    
   },
   {
      name: 'assets-create',
      path: '/assets/create',
      component: () => import('../views/asset/Edit'), 
      meta: { 
         order: 0,
         icon: '',
         title: 'Create Asset',
         type: 'all',
         menu: ''
      }    
   },
   {
      name: 'assets-edit',
      path: '/assets/:id',
      component: () => import('../views/asset/Edit'), 
      meta: { 
         order: 0,
         icon: '',
         title: 'Edit Asset',
         type: 'all',
         menu: ''
      }    
   }
];

const guestLinks = [];

const userLinks = [];

let appRoutes = links.concat(guestLinks).concat(userLinks);

for(let i = 0; i < appRoutes.length; i++){
   appRoutes[i].meta.order = i;
}

export default appRoutes;