import app from './app.module';
import deployment from './deployment.module';
import client from './client.module';
import asset from './asset.module';
import trade from './trade.module';

Vue.use(Vuex);

export default new Vuex.Store({
   loading: false,
   modules: {
      app,
      deployment,
      client,
      asset,
      trade
   }
});
