import AssetService from '../services/asset';
import Helper from '@/common/helper';

import {
   FETCH_ASSETS, CREATE_ASSET, STORE_ASSET, 
   EDIT_ASSET, UPDATE_ASSET, DELETE_ASSET
} from './actions.type';

import { SET_LOADING } from './mutations.type';

const initialState = {
  
};

export const state = { ...initialState };
 
const getters = {
   
};


const actions = {
   [FETCH_ASSETS](context, params = null) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         AssetService.fetch(params)
            .then(assets => {
               resolve(assets);
            })
            .catch(error => {
               reject(error);
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [CREATE_ASSET](context) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         AssetService.create()
            .then(model => {
               resolve(model);
            })
            .catch(error => {
               reject(error);      
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [STORE_ASSET](context, model) {
      context.commit(SET_LOADING, true);
      console.log(model);
      return new Promise((resolve, reject) => {
         AssetService.store(model)
            .then(id => {
               resolve(id);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [EDIT_ASSET](context, id) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         AssetService.edit(id)
            .then(model => {
               resolve(model);
            })
            .catch(error => {
               reject(error);        
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [UPDATE_ASSET](context, model) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         AssetService.update(model)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error)); 
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   [DELETE_ASSET](context, id) {
      context.commit(SET_LOADING, true);
      return new Promise((resolve, reject) => {
         AssetService.remove(id)
            .then(() => {
               resolve(true);
            })
            .catch(error => {
               reject(Helper.resolveErrorData(error));
            })
            .finally(() => { 
               context.commit(SET_LOADING, false);
            });
      });
   },
   
   
};


const mutations = {
   
};

export default {
   state,
   actions,
   mutations,
   getters
};
 